import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

import { Api } from '../api/api';

/**
 * Class Nomination
 */
@Injectable()
export class Nomination {
  constructor(public api: Api, public storage: Storage) { }

  /**
   * Method consult
   * @param body any
   */
  public consult(body: any) {
    let seq = this.api.post('verificator/validation', body).share();

    seq.subscribe((response: any) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }

  /**
   * Method validator
   * @param body any
   */
  public validator(body: any) {
    let seq = this.api.post('verificator/validation', body).share();

    seq.subscribe((response: any) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }

  /**
   * Method next
   * @param body any
   */
  public next(body: any) {
    let seq = this.api.post('verificator/validation', body).share();

    seq.subscribe((response) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }

  /**
   * Method send
   * @param body any
   */
  public send(body: any) {
    let seq = this.api.post('verificator/validation', body).share();

    seq.subscribe((response) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }

  /**
   * Method sendFile
   * @param file any
   * @param options any
   */
  public sendFile(file: any, options: any) {
    let seq = this.api.upload(file, 'verificator/ReceiveFile', options);

    seq.then((response) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }

  /**
   * Method departments
   */
  public departments() {
    let seq = this.api.get('verificator/DepartmentList').share();

    seq.subscribe((response) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }

  /**
   * Method department
   * @param id any
   */
  public department(id: any) {
    let seq = this.api.get('verificator/GetDepartment?id=' + id).share();

    seq.subscribe((response) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }

  /**
   * Method cities
   * @param departament any
   */
  public cities(departament) {
    let seq = this.api.get('verificator/CityList?departmentId=' + departament).share();

    seq.subscribe((response) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }

  /**
   * Method city
   * @param id any
   */
  public city(id: any) {
    let seq = this.api.get('verificator/GetCity?id=' + id).share();

    seq.subscribe((response) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }

  /**
   * Method hermeco
   * @param document any
   * @param type any
   */
  public hermeco(document: any, type: any) {
    let seq = this.api.get('verificator/ConsultDocument?document=' + document + '&&type=' + type).share();

    seq.subscribe((response) => {
      if (response) {
        console.log(response);
      }
    }, error => {
      console.log(error);
    });

    return seq;
  }
}



