import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { HttpClient } from "@angular/common/http";
import { Transfer, TransferObject } from "@ionic-native/transfer";

/**
 * Class Api
 * REST Api handler
 */
@Injectable()
export class Api {
  url: string = "https://identigohermecoapi.enterdev.co/api";
  // url: string = 'http://localhost:49540/api';
  // url: string = 'http://192.168.1.25:49540/api';
  // url: string = 'http://192.168.6.134:49540/api';
  protected results: any;

  constructor(public http: HttpClient, private transfer: Transfer) { }

  /**
   * Method get
   * @param endpoint string
   */
  public get<T>(endpoint: string): Observable<T> {
    return this.http.get<T>(this.url + "/" + endpoint);
  }

  /**
   * Method post
   * @param endpoint string
   * @param body string
   */
  public post<T>(endpoint: string, body: string): Observable<T> {
    return this.http.post<T>(this.url + "/" + endpoint, body);
  }

  /**
   * Method put
   * @param endpoint string
   * @param body any
   */
  public put<T>(endpoint: string, body: any): Observable<T> {
    return this.http.put<T>(this.url + "/" + endpoint, body);
  }

  /**
   * Method delete
   * @param endpoint string
   */
  public delete<T>(endpoint: string): Observable<T> {
    return this.http.delete<T>(this.url + "/" + endpoint);
  }

  /**
   * Method patch
   * @param endpoint string
   * @param body any
   */
  public patch<T>(endpoint: string, body: any): Observable<T> {
    return this.http.patch<T>(this.url + "/" + endpoint, body);
  }

  /**
   * Method upload
   * @param file any
   * @param endpoint string
   * @param options any
   */
  public upload(file: any, endpoint: string, options: any) {
    if (options.params.RunningBrowser) {
      return this.browserUpload(
        file,
        encodeURI(this.url + "/" + endpoint),
        options.params
      );
    } else {
      const fileTransfer: TransferObject = this.transfer.create();

      return fileTransfer.upload(
        file,
        encodeURI(this.url + "/" + endpoint),
        options
      );
    }
  }

  /**
   * Upload the file to the endpoint when the action comes from the browser
   *
   * @private
   * @param {File} file_to_upload
   * @param {string} url
   * @returns
   * @memberof Api
   */
  async browserUpload(
    file: File,
    url: string,
    params: object
  ) {
    let promise = new Promise((resolve, reject) => {
      const body: FormData = new FormData();

      body.append("fileKey", file, file.name);

      for (const index in params) {
        body.append(index.toLowerCase(), params[index]);
      }

      this.http.post(url, body)
        .toPromise()
        .then(
          res => {
            this.results = res;
            resolve(this);
          },
          msg => {
            reject(msg);
          }
        );
    });

    return await promise.then(function(result: any) {
      return { response: JSON.stringify(result.results) };
    });
  }
}



